<template>

  <imp-panel>
    <h3 class="box-title" slot="header" style="width: 100%;">
      <el-form :inline="true" :model="form" ref="form">
        <el-form-item label="" size="small">
          <el-input style="width: 140px;" size="small" v-model="searchItem.no" placeholder="" icon="search"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input  style="width: 140px;" size="small" v-model="searchItem.name" placeholder="" icon="search"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input  style="width: 140px;" size="small" v-model="searchItem.name" placeholder="" icon="search"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-select-tree default-expand-all ref="tree" v-model="filterText" :treeData="menuTree" :propNames="defaultProps" clearable placeholder="" :filter-node-method="filterNode">
          </el-select-tree>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" size="small"  @click="search"></el-button>
        </el-form-item>
        <el-form-item style="float: right;">
          <el-button type="primary" icon="plus" @click="addRole()"></el-button>
        </el-form-item>
      </el-form>
    </h3>
    <el-row slot="body" :gutter="24" style="margin-bottom: 20px;">
    <el-table :data="tableData" style="width: 100%;">
      <el-table-column prop="userCode" label="" width="180"></el-table-column>
      <el-table-column prop="userName" label="" width="180"></el-table-column>
      <el-table-column prop="password" label=""></el-table-column>
      <el-table-column prop="phone" label=""></el-table-column>
      <el-table-column label="">
        <template scope="scope">
          <el-popover ref="popoverDetail" placement="top-start" width="200" trigger="click">
            <el-form :model="form">
              <el-form-item label="" style="margin-bottom: 0px;">
                <span v-model="form.no">12121</span>
              </el-form-item>
              <el-form-item label="" style="margin-bottom: 0px;">
                <span v-model="form.name"></span>
              </el-form-item>
              <el-form-item label="" style="margin-bottom: 0px;">
                <span type="textarea" v-model="form.remarks"></span>
              </el-form-item>
            </el-form>
          </el-popover>
          <el-button size="small" icon="document" v-popover:popoverDetail></el-button>
          <el-button size="small" icon="edit" @click="addRole(scope.$index)"></el-button>
          <el-button size="small" type="danger" icon="delete" @click="deleteRole(scope.$index, tableData)"></el-button>
        </template>
      </el-table-column>
    </el-table>
    </el-row>
  </imp-panel>

</template>
<script>

  import panel from "../../components/panel.vue"
  import selectTree from "../../components/selectTree.vue"
  import treeter from "../../components/treeter"


  import * as api from "../../api"

  export default {
    mixins: [treeter],
    components: {
      'imp-panel': panel,
      'el-select-tree': selectTree,
    },
    watch: {
      filterText(val){
        this.$refs.tree.filter(val);
      }
    },
    data(){
      return {
        filterText: '',
        form: {
          id: null,
          no: '',
          name: '',
          remarks: '',
        },
        searchItem: {
          no: "",
          name: "",
          type: "",
          parentId: ""
        },
        menuTree: [{
          label: '',
          children: [{
            label: ''
          },{
            label: ''
          },{
            label: ''
          }]
        },{
          label: '',
          children: [{
            label: ''
          },{
            label: '',
            children: [{
              label: ''
            }]
          },{
            label: '',
            children: [{
              label: ''
            },{
              label: ''
            },{
              label: ''
            },{
              label: ''
            },{
              label: ''
            },{
              label: ''
            },{
              label: ''
            },{
              label: ''
            },{
              label: ''
            }]
          }]
        }],
        defaultProps: {
          children: 'children',
          label: 'label'
        },

        tableData: [],
      }
    },
    methods: {
      filterNode(value, data){
        if(!value) return true;
        return data.label.indexOf(value) !== -1;
      },
      addRole(index){
        console.log("index === "+ index);
        if(index == undefined){
          this.$router.push({path: 'userAddOrEdit'});
        } else {
          this.$router.push({path: 'userAddOrEdit', query: {id: this.tableData[index].id}});
        }
      },
      deleteRole(index, rows){
        // eclipse
        if( api.CONTEXT === "./power-web") {
          var roleId = rows[index].id;
          var params = new URLSearchParams();
          params.append("roleCode", this.form.no);
          this.$http.post(api.USER_DELETE, params).then(res => {
            var data = res.data;
            var isSuccess = data.success;
            if(data.success) {
              rows.splice(index, 1);
              this.$message({
                message: '',
                type: 'success'
              });
            } else {
              this.$message.error('');
            }
          })
        } else {
        // 
          var data = {"data":null,"success":true,"message":""};
          var isSuccess = data.success;
          if(data.success) {
            rows.splice(index, 1);
            this.$message({
              message: '',
              type: 'success'
            });
          } else {
            this.$message.error('');
          }
        }
      },
      search(){
        var params = new URLSearchParams();
        params.append("roleCode", this.searchItem.no);
        params.append("roleName", this.searchItem.name);
        this.load(params);
      },
      load(params) {
        if (api.CONTEXT === "./power-web") {
          this.$http.get(api.USER.SELECT, params).then( res => {
            console.log(res.data);
            this.tableData = res.data.data.data;
          })
        } else {
          var data = {"data":{"data":[{"id":7,"isDeleted":0,"userCode":"002","userName":"SS","password":"SSS","realName":"SSS","sex":null,"birthday":null,"phone":"SSS","address":null,"deptId":null,"remark":null},{"id":5,"isDeleted":0,"userCode":"002","userName":"SS1","password":"SSS","realName":"SSS","sex":null,"birthday":null,"phone":"SSS","address":null,"deptId":null,"remark":null},{"id":3,"isDeleted":0,"userCode":"001","userName":"aaa1","password":"aaa","realName":"","sex":null,"birthday":null,"phone":"15888888888","address":null,"deptId":null,"remark":null},{"id":1,"isDeleted":0,"userCode":"01","userName":"aaa","password":"bbb","realName":"","sex":1,"birthday":"1959-10-01 00:00:00","phone":null,"address":null,"deptId":0,"remark":""}],"total":4,"nowpage":0,"pagesize":6,"rowStart":0,"rowEnd":6,"order":"desc","sort":"id","condition":null},"success":true,"message":""};
          this.tableData = data.data.data;
        }
      }
    },
    created(){
      this.load();
    }
  }
</script>

<style>
  .render-content {
    float: right;
    margin-right: 20px
  }

  .render-content i.fa {
    margin-left: 10px;
  }

  .render-content i.fa:hover{
    color: #e6000f;
  }

  .select-tree .el-scrollbar.is-empty .el-select-dropdown__list {
    padding: 0;
  }

  .select-tree .el-scrollbar {
    border: 1px solid #d1dbe5;
  }

  .select-tree .el-tree{
    border:0;
  }

</style>



